import Vue from 'vue'
import Vuex from 'vuex'
import * as types from './home/types'
import * as posts from './home/posts'
import * as stories from './home/stories'
import * as poems from './home/poems'
import * as search from './home/search'
import * as about from './home/about'


Vue.use(Vuex)

export default new Vuex.Store({
 
  modules: {
    types,
    posts,
    stories,
    poems,
    search,
    about
  }
})
