<template>
  <template-view>
    <!-- Hero Section -->
    <section>
      <div class="container" style="height:90vh">
        <div class="d-flex justify-content-center align-items-center">
          <div>
            <h1>Welcome to <span class="text-danger">Sayari</span></h1>
            <br>
            <p>
              Sayari is an online literary community where readers and writers
              of original poetry and stories can interact.
            </p>
            <br>
            <a
              class="btn btn-outline-danger ms-2"
             href="/register"
            >
              <i class="fa-solid fa-pencil"></i>Start Writing
            </a>
          </div>
          <div>
            <img
              src="@/assets/hero-img.jpg"
              alt=""
              height="470px"
              width="620px"
            />
          </div>
        </div>
      </div>
    </section>

    <!-- Explorer section -->
    <section class="bg-light py-5">
      <div class="container" style="height:90vh">
        <div class="row">
          <div class="col-md-6">
            <img src="@/assets/explore.png" class="img-fluid" style="over-flow:hidden" alt="" />
          </div>
          <div class="col-md-6 d-flex align-items-center">
            <div>
              <h1>Let your story be discovered</h1>
              <p>
                If you're a writer looking to share your stories and poems with others, Sayari is the perfect platform for you. With Sayari, you can create an account and start submitting your work right away. Once your work is published on the platform, other users can discover it, read it, and provide feedback.
              </p>
            </div>
          </div>
        </div>
      </div>
    </section>

    <!-- Latest Section -->
    <section class="py-5" v-for="(type, index) in types" :key="index">
      <div class="container">
        <div class="d-flex justify-content-between">
          <div>
            <h1>{{ type.name }}</h1>
            <p>
              Get Latest updates
            </p>
          </div>
          <div>
            <vs-button danger border:active="active == 2" @click.prevent="$router.push({name:'poems'})" v-if="type.slug == 'poems'">
              View More
            </vs-button>

            <vs-button danger border:active="active == 2" @click.prevent="$router.push({name:'stories'})" v-else>
              View More
            </vs-button>
          </div>
        </div>
        <div class="row g-2">
          <div class="col-md-12">
            <carousel :autoplay="true" :nav="false" :margin="5" :responsive="{0:{items:1,nav:false},600:{items:5,nav:true}}">
                <div v-for="(post,index) in type.posts" :key="index">
                  <el-card shadow="hover" v-if="post.status == 'approved'" style="height:350px">
                    <a href="" @click.prevent="$router.push({name:'post',params:{id:post.id}})"><img :src="post.image" alt="" style="height:200px;objectFit:cover;width:100%"></a>
                    <div>
                      <span class="fw-bold">{{ post.title }}</span>
                    </div>
                    <div>
                      <span style="fontSize:12px">Writer: {{ post.writer }}</span>
                    </div>
                    <div v-if="post.price == 'Free'">
                      <span class="badge bg-danger">{{ post.price }}</span>
                    </div>
                    <div v-else>
                      <span>Rs.{{ post.price }}</span>

                    </div>
                  </el-card>
                </div>
            </carousel>
          </div>
        </div>
      </div>
    </section>

    <!--Feedback  -->  

    <section class="bg-light py-5">
      <div class="container">
        <div class="row border border-2 p-5">
          <h2 class="pb-5">Give your feedback</h2>
          <div class="col-md-12">
            <div >
              <div class="form-group">
                <label for="">Enter your name</label>
                <input type="text" v-model="feedback.name" class="form-control" placeholder="" aria-describedby="helpId">
              </div>

              <div class="form-group">
                <label for="">Email</label>
                <input type="email" v-model="feedback.email" class="form-control" placeholder="" aria-describedby="helpId">
              </div>

              <div class="form-group">
                <label for="">Message</label>
                <textarea class="form-control" v-model="feedback.message" rows="3"></textarea>
              </div>

            <br>
              <button @click.prevent="sendFeedback()" class="btn btn-danger py-1">Send your feedback</button>
            </div>
          </div>
        </div>
      </div>
    </section>
  </template-view>
</template>

<script>
import carousel from "vue-owl-carousel";
import TemplateView from "./TemplateView.vue";
import { mapGetters } from "vuex";
import axiosApi from '../config/axios';
export default {
  components: { TemplateView, carousel },
  data(){
    return {
      feedback:{
        name : "",
        email : "",
        message : ""
      }
    }
  },
  computed: {
    ...mapGetters({
      types: "get_types",
    }),
  },

  methods:{
    async sendFeedback(){
      try{
        var response = await axiosApi.post("feedback",this.feedback)
        if(response.status == 200){
          alert("Thank you for your feedback")
        }
      }catch(e){
        console.warn(e)
      }
    }
  }
};
</script>

<style scoped>
h1 {
  color: black;
  font-weight: bold;
}
p {
  color: black;
  font-weight: 500;
  font-size: 20px;
}

.card:hover {
  background-color: #f1f1f1;
  box-shadow: 5px 5px 5px #aaaaaa;
}
</style>
