import axiosApi from "@/config/axios"

export const state = {
    stories : []
}
export const getters = {
    get_stories : (state) => state.stories
}
export const mutations = {
    set_stories : (state,payload) => state.stories = payload

}
export const actions = {
    async fetchStories({commit}){
        try{
            var response = await axiosApi.get('stories')
            if(response.status == 200){
                commit('set_stories',response.data.data)
            }
        }catch(e){
            console.warn(e)
        }
    }
}