import axiosApi from "@/config/axios"

export const state = {
    search : []
}
export const getters = {
    get_search : (state) => state.search
}
export const mutations = {
    set_search : (state,payload) => state.search = payload

}
export const actions = {
    async fetchSearch({commit},query){
        try{
            var response = await axiosApi.get(`search?query=${query}`)
            if(response.status == 200){
                commit('set_search',response.data.data)
            }
        }catch(e){
            console.warn(e)
        }
    }
}