<script>
import { mapActions } from 'vuex';
export default {
  name: "NavbarComponent",
  data(){
    return{
      query: ""
    }
  },
  methods:{
    ...mapActions(['fetchSearch'])
  }
};
</script>

<style scoped>
.navbar {
  font-family: "Bitter";
}
a {
  font-size: 18px;
  color: black;
  font-weight: bold;
}
.bg-primary {
  background-color: var(--primary) !important;
}
</style>
