import axiosApi from "@/config/axios"

export const state = {
    poems : []
}
export const getters = {
    get_poems : (state) => state.poems
}
export const mutations = {
    set_poems : (state,payload) => state.poems = payload

}
export const actions = {
    async fetchPoems({commit}){
        try{
            var response = await axiosApi.get('poems')
            if(response.status == 200){
                commit('set_poems',response.data.data)
            }
        }catch(e){
            console.warn(e)
        }
    }
}